<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {}
  },
  <span class="hljs-attr">computed</span>: {
    dateDisabled () {
      <span class="hljs-keyword">return</span> <span class="hljs-function">(<span class="hljs-params">date</span>) =&gt;</span> {
        <span class="hljs-keyword">let</span> data = <span class="hljs-keyword">this</span>.date2object(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>())
        <span class="hljs-keyword">return</span> date.getTime() &lt; <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(data.year, data.month, data.date, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>).getTime()
      }
    },
    timeDisabled () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">hour</span>: <span class="hljs-function">(<span class="hljs-params">date</span>) =&gt;</span> {
          <span class="hljs-keyword">if</span> (date.getFullYear() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getFullYear() &amp;&amp; date.getMonth() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getMonth() &amp;&amp; date.getDate() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getDate()) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getHours() - <span class="hljs-number">1</span>)
          } <span class="hljs-keyword">else</span> {
