        },
        <span class="hljs-attr">minute</span>: <span class="hljs-function">(<span class="hljs-params">date</span>) =&gt;</span> {
          <span class="hljs-keyword">if</span> (date.getFullYear() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getFullYear() &amp;&amp; date.getMonth() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getMonth() &amp;&amp; date.getDate() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getDate() &amp;&amp; date.getHours() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getHours()) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getMinutes() - <span class="hljs-number">1</span>)
          } <span class="hljs-keyword">else</span> {
            <span class="hljs-keyword">return</span> []
          }
        },
        <span class="hljs-attr">second</span>: <span class="hljs-function">(<span class="hljs-params">date</span>) =&gt;</span> {
          <span class="hljs-keyword">if</span> (date.getFullYear() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getFullYear() &amp;&amp; date.getMonth() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getMonth() &amp;&amp; date.getDate() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getDate() &amp;&amp; date.getHours() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getHours() &amp;&amp; date.getMinutes() === <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getMinutes()) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.range(<span class="hljs-number">0</span>, <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getSeconds())
