            <span class="hljs-keyword">return</span> []
          }
        }
      }
    }
  },
  <span class="hljs-attr">methods</span>: {
    range (staft, end) {
      <span class="hljs-keyword">let</span> res = []

      <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = staft; i &lt;= end; i++) {
        res.push(i)
      }

      <span class="hljs-keyword">return</span> res
    },
    <span class="hljs-keyword">typeof</span> (data) {
      <span class="hljs-keyword">return</span> <span class="hljs-built_in">Object</span>.prototype.toString.call(data).slice(<span class="hljs-number">8</span>, <span class="hljs-number">-1</span>).toLowerCase()
    },
    date2object (date) {
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.typeof(date) !== <span class="hljs-string">'date'</span>) <span class="hljs-keyword">return</span> <span class="hljs-literal">null</span>

      <span class="hljs-keyword">let</span> _data = {
        <span class="hljs-attr">year</span>: date.getFullYear(),
        <span class="hljs-attr">month</span>: date.getMonth(),
        <span class="hljs-attr">date</span>: date.getDate(),
        <span class="hljs-attr">hour</span>: date.getHours(),
        <span class="hljs-attr">minute</span>: date.getMinutes(),
        <span class="hljs-attr">second</span>: date.getSeconds(),
        <span class="hljs-attr">times</span>: date.getTime(),
        <span class="hljs-attr">day</span>: date.getDay()
      }
      _data[<span class="hljs-string">'days'</span>] = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(_data.year, _data.month + <span class="hljs-number">1</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>).getDate()
      _data[<span class="hljs-string">'prevMonthDays'</span>] = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(_data.year, _data.month, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>).getDate()
      _data[<span class="hljs-string">'prevDays'</span>] = _data.day
      _data[<span class="hljs-string">'nextDays'</span>] = <span class="hljs-number">6</span> * <span class="hljs-number">7</span> - _data.days - _data.prevDays

      <span class="hljs-keyword">return</span> _data
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
